@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
  /* background-color: #ffecd2 ; */
  background-image: linear-gradient(315deg, #0f2027 0%, #203a43 85%);
}

.header {
  background-image: linear-gradient(315deg, #13547a 0%, #80d0c7 85%);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 7rem;
  padding: 1rem 1rem 1rem 1rem;
}

h1 {
  font-size: 60px;
  color: #020300;
}

.sortName {
  font-family: inherit;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  padding: 5px;
  color: whitesmoke;
}

.btn {
  font-family: inherit;
  display: inline-block;
  padding: 0.5em 3em;
  border: 0.16em solid #ffffff;
  margin: 0 0.3em 0.3em 0;
  text-transform: uppercase;
  color: #020300;
  font-weight: bold;
  border-radius: 3px;
  text-align: center;
  transition: all 0.15s;
  outline: none;
  cursor: pointer;
}
.btn:hover {
  background-color: #dddddd;
}
.btn:active {
  color: #bbbbbb;
}

.sort-type {
  padding: 0.5rem 0;
  display: flex;
  justify-content: center;
}

.array-holder {
  padding: 25px 75px;
  display: flex;
  /* align-items: center; */
  align-items: flex-end;
  justify-content: center;
  height: 750px;
}

.array-bar {
  width: 10px;
  margin: 0 1px;
  background-color: #40dfdf;
  border-radius: 2px;
}

